@import "mixins";


@media all  and (max-width: 768px) {
	body {
		min-width: 402px; }

	section {
		max-width: 640px; }

	.desktop {
		display: none; }
	.mobile {
		display: block; }

	.image {
		width: 335px;
		height: 354px;
		margin-right: 20px;
		margin-left: 20px;
		box-sizing: border-box; }
	.info {
		width: 37%; }

	.share {
		span {
			display: none; } }

	.toggle {
		display: block;
		background: none;
		position: absolute;
		top: -10px;
		left: -360px;

		.share-h, .close {
			display: none; }

		.social {
			display: block;
			margin-top: 10px;
			margin-bottom: 10px; } }

	.grid {
		text-align: center;
		.product {
			width: 280px;
			height: 280px;
			margin: 0 40px 40px 0 !important; }

		.product:nth-child(2),
		.product:nth-child(4),
		.product:nth-child(6) {
			margin-right: 0px !important; }

		.price {
			width: 92.8%;
			bottom: 0; }
		.price:before {
			opacity: 0.7;
			bottom: 0; } } }


@media all  and (max-width: 620px) {
	body {
		min-width: 402px; }
	section {
		max-width: 402px; }

	.product {
		h2 {
			width: 330px;
			margin: 20px auto; } }

	.image {
		width: 100%;
		max-width: 402px;
		height: 420px;
		margin-right: 0; }

	.info {
		width: 100%;
		max-width: 330px;
		margin: auto;
		display: block; }
	.id {
		font-size: 12px; }
	.price {
		font-size: 36px;
		margin-top: 15px; }
	.rating {
		margin-top: 15px;
		margin-bottom: 0; }
	.share {
		margin-left: 0; }
	.toggle {
		padding: 0;
		margin: 20px 0;
		display: inline-block;
		position: relative;
		top: 0px;
		left: 0;
		.social {
			display: inline-block; }
		.social:nth-child(3) {
			margin-left: 0; } }
	.details {
		font-size: 14px; }
	.size {
		text-align: center;
		.size-caption {
			margin: 10px 10px; } }
	input[type="submit"] {
		position: relative;
		left: 50%;
		@include translate(-50%, 0); }


	.grid {
		h2 {
			text-align: center; }

		#slider {
			position: relative;
			input {
				display: block; } }
		#slides {
			width: 402px; }
		.inner {
			width: 1800px; }

		.inner:after {
			content: '';
			display: block;
			height: 0;
			clear:both {} }

		.product {
			float: left;
			margin: 0 !important;
			width: 280px; }

		#overflow {
			width: 280px;
			margin: auto auto;
			overflow: hidden; }

		#slider input {
			display: none; }

		#controls {
			position: absolute;
			width: 384px;
			top: 50%;
			@include translate(5px, -50%);
			height: 50px;
			z-index: 100; }

		#controls label {
			display: none;
			cursor: pointer; }

		#controls label:hover {
			opacity: 0.8; }

		#slide1:checked ~ #controls label:nth-child(2),
		#slide2:checked ~ #controls label:nth-child(3),
		#slide3:checked ~ #controls label:nth-child(4),
		#slide4:checked ~ #controls label:nth-child(5),
		#slide5:checked ~ #controls label:nth-child(6),
		#slide6:checked ~ #controls label:nth-child(1) {
			width: 50px;
			height: 50px;
			background: url('/img/right.png') no-repeat center center;
			float: right;
			margin-right: -5px;
			display: block; }

		#slide1:checked ~ #controls label:nth-child(6),
		#slide2:checked ~ #controls label:nth-child(1),
		#slide3:checked ~ #controls label:nth-child(2),
		#slide4:checked ~ #controls label:nth-child(3),
		#slide5:checked ~ #controls label:nth-child(4),
		#slide6:checked ~ #controls label:nth-child(5) {
			width: 50px;
			height: 50px;
			background: url('/img/left.png') no-repeat center center;
			float: left;
			display: block; }

		#slide1:checked ~ #slides .inner {
			margin-left: 0; }

		#slide2:checked ~ #slides .inner {
			margin-left: -100%; }

		#slide3:checked ~ #slides .inner {
			margin-left: -200%; }

		#slide4:checked ~ #slides .inner {
			margin-left: -300%; }

		#slide5:checked ~ #slides .inner {
			margin-left: -400%; }

		#slide6:checked ~ #slides .inner {
			margin-left: -500%; }

		#active {
			text-align: center;
			margin-top: 10px; }

		#active label {
			width: 10px;
			height: 10px;
			background: #ccc;
			display: inline-block;
			cursor: pointer;
			border-radius: 2px;
			-webkit-border-radius: 2px;
			-moz-border-radius: 2px; }

		#active label:hover {
			background: #999; }

		#slide1:checked ~ #active label:nth-child(1),
		#slide2:checked ~ #active label:nth-child(2),
		#slide3:checked ~ #active label:nth-child(3) {
			background: #333; }

		#slides .inner {
			@include transition-ease(0.5s); } } }
